<script>
export default {
  data() {
    return {
      drawer: false,
        direction: 'rtl',
      activeIndex2: '1',
      isCollapse: false,
      topnavleft:'200px',
      topnavwidth:'1080px',
      activities: [{
          content: '发行版',
          timestamp: '2022-04-12 20:46',
          size: 'large',
          type: '#0bbd87'
        }, {
          content: '正式版1.0',
          timestamp: '2023-01-03 12:46',
          color: '#0bbd87'
        }, {
          content: '正式版1.0.1',
          timestamp: '2023-01-13 13:46',
          color: '#0bbd87'
        }, {
          content: '正式版2.0.1',
          timestamp: '2023-02-03 06:46',
          color: '#0bbd87'
        }],
      listName:['系统设置','数据表格','密码修改','数据库','数据','模板','其它'],
      items:'数据'
    };
  },
  methods: {
    Close(done) {
        this.$confirm('确认关闭？')
          .then(_ => {
            done();
          })
          .catch(_ => {});
      },
    show(){
      this.isCollapse = !this.isCollapse
      if(this.isCollapse){
        this.topnavleft = '64px'
        this.topnavwidth = '1216px'
      }else{
        this.topnavleft = '200px'
        this.topnavwidth = '1080px'
      }
    },
    tuichu() {
      this.$confirm('即将退出账号, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.$router.push('/Login');
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消'
          });          
        });
    },
    handleSelect(key, keyPath) {
      this.items = this.listName[key-2]
    },
    handleOpen(key, keyPath) {
      console.log(key, keyPath);
    },
    handleClose(key, keyPath) {
      console.log(key, keyPath);
    }
  }
}
</script>

