import Vue from 'vue'
import VueRouter from 'vue-router'
import Login from '@/components/Login.vue'
import Main from '@/components/Main.vue'
import shuju from '@/components/leftnav/shuju.vue'
import moban from '@/components/leftnav/moban.vue'
import qita from '@/components/leftnav/qita.vue'
import xit from '@/components/leftnav/xitong/xitong.vue'
import xitsetting from '@/components/leftnav/xitong/setting.vue'
import xitshujubiao from '@/components/leftnav/xitong/shujubiaoge.vue'
import xituser from '@/components/leftnav/xitong/user.vue'
import xitshujuku from '@/components/leftnav/xitong/shujuku.vue'




Vue.use(VueRouter)

const routes = [
  {path:'/',redirect:'/Login'},
	{path:'/Login',component:Login},
	{path:'/Main',component:Main,children:[
    {path:'',component:shuju},
    {path:'shuju',component:shuju},
    {path:'moban',component:moban},
    {path:'qita',component:qita},
    {path:'xitong',component:xit,children:[
      {path:'',component:xitsetting},
      {path:'xitsetting',component:xitsetting},
      {path:'xitshujubiao',component:xitshujubiao},
      {path:'xituser',component:xituser},
      {path:'xitshujuku',component:xitshujuku}

    ]},

  ]}
]

const router = new VueRouter({
  routes
})

export default router
