<template>
  <div class="content-setting">
    <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
      <el-form-item label="标题" prop="name">
        <el-input v-model="ruleForm.name" placeholder="请输入标题"></el-input>
      </el-form-item>
      <el-form-item label="栏目" prop="name">
        <el-input v-model="ruleForm.lanmu" placeholder="请输入栏目"></el-input>
      </el-form-item>
      <el-form-item label="所在城市" prop="region">
        <el-select v-model="ruleForm.region" placeholder="请选择活动区域">
          <el-option label="江苏/南京/建邺区" value="jsu"></el-option>
          <el-option label="江西/九江/濂溪区" value="jxi"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="性别">
        <el-radio v-model="radio" label="1">男</el-radio>
        <el-radio v-model="radio" label="2">女</el-radio>
      </el-form-item>
      <el-form-item label="日期" prop="date1">
        <el-date-picker v-model="ruleForm.date1" type="date" placeholder="选择日期">
        </el-date-picker>
      </el-form-item>
      <el-form-item label="时间" prop="date2">
        <el-time-select v-model="ruleForm.date2" :picker-options="{
          start: '08:30',
          step: '00:15',
          end: '18:30'
        }" placeholder="选择时间">
        </el-time-select>
      </el-form-item>
      <el-form-item label="多组选项" prop="type">
        <el-checkbox>选项1</el-checkbox>
        <el-checkbox>选项2</el-checkbox>
        <el-checkbox>选项3</el-checkbox>
        <el-checkbox>选项4</el-checkbox>
      </el-form-item>
      <el-form-item label="评分">
        <el-rate v-model="value1"></el-rate>
      </el-form-item>
      <el-form-item label="文件上传">
        <el-button size="small" type="primary">点击上传</el-button>
        <div slot="tip" class="el-upload__tip">只能上传jpg/png文件，且不超过500kb</div>
      </el-form-item>
      <el-form-item label="内容">
        <el-input type="textarea" autosize placeholder="请输入内容" v-model="textarea1">
        </el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="submitForm('ruleForm')">提交</el-button>
        <el-button @click="resetForm('ruleForm')">重置</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
export default {
  data() {
    return {
      ruleForm: {
        name: '',
        lanmu: '',
        region: '',
        date1: '',
        date2: '',
        delivery: false,
        type: [],
        resource: '',
        desc: ''
      },
      rules: {
        name: [
          { required: true, message: '请输入活动名称', trigger: 'blur' },
          { min: 3, max: 5, message: '长度在 3 到 5 个字符', trigger: 'blur' }
        ],
        region: [
          { required: true, message: '请选择活动区域', trigger: 'change' }
        ],
        date1: [
          { type: 'date', required: true, message: '请选择日期', trigger: 'change' }
        ],
        date2: [
          { type: 'date', required: true, message: '请选择时间', trigger: 'change' }
        ],
        type: [
          { type: 'array', required: true, message: '请至少选择一个活动性质', trigger: 'change' }
        ],
        resource: [
          { required: true, message: '请选择活动资源', trigger: 'change' }
        ],
        desc: [
          { required: true, message: '请填写活动形式', trigger: 'blur' }
        ]
      }
    };
  },
  methods: {
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          alert('submit!');
        } else {
          console.log('error submit!!');
          return false;
        }
      });
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    }
  }
}
</script>

<style lang="less" scoped>
.content-setting {
  width: 100%;
  height: 100%;
  padding-top: 15px;
}
/deep/.el-form-item__label {
    font-size: 9px;
    line-height: 20px;
}
/deep/.el-input__inner {
    height: 23px;
    line-height: 23px;
    font-size: 10px;
    width: 96%;
  }
/deep/.el-form-item__content {
    line-height: 20px;
  }
  /deep/.el-input__icon {
    line-height: 20px;
}
/deep/.el-button{
  padding: 6px 5px;
  font-size: 10px;
}
/deep/.el-checkbox__label {
    padding-left: 2px;
    font-size: 10px;
}
/deep/.el-radio__label {
  font-size: 10px;
    padding-left: 3px;
}
/deep/.el-textarea__inner {
    padding: 5px 5px;
    font-size: 10px;
    width: 96%;

    }
</style>